import React from 'react'
import { useNavigate } from 'react-router-dom'
import { useUserStore } from 'src/userStore';
import { AppContent, AppSidebar, AppFooter, AppHeader } from '../components/index'
import Constants, { userToken } from '../Constants';

const DefaultLayout = () => {
  const navigation = useNavigate()
  const cookie = React.useRef(document.cookie)
  const [valid, setValid] = React.useState(false)
  const setUser = useUserStore((state) => state.setUser)
  React.useEffect(() => {
    if(!cookie.current.includes('_token=')) {
      navigation('/login')
    }
    (async () => {
      let _token = userToken()
      let url = Constants.me;
      let response = await fetch(url, {
        method: 'GET',
        headers: {
          Authorization: _token
        }
      })
      let data = await response.json()
      if(data.status === 403) {
        document.cookie = `_token=; expires=Thu, 01 Jan 1970 00:00:00 UTC; path="/";max-age=0;`;
        navigation('/login')
      } else {
        setUser(data.data);
        setValid(true)
      }
    })();
  }, [navigation, setUser])

  if(!cookie.current.includes('_token=') || !valid) {
    return <></>
  }
  return (
    <div>
      <AppSidebar />
      <div className="wrapper d-flex flex-column min-vh-100 bg-light">
        <AppHeader />
        <div className="body flex-grow-1 px-3">
          <AppContent />
        </div>
        <AppFooter />
      </div>
    </div>
  )
}

export default DefaultLayout
