import React from 'react'
import {
  CDropdown,
  CDropdownDivider,
  CDropdownItem,
  CDropdownMenu,
  CDropdownToggle,
} from '@coreui/react'
import { cilLockLocked, cilAccountLogout, cilUser } from '@coreui/icons'
import CIcon from '@coreui/icons-react'
import { useNavigate } from 'react-router-dom'
import { useUserStore } from 'src/userStore'

const AppHeaderDropdown = () => {

  const type = useUserStore((state) => state.type())
  const name = useUserStore((state) => state.name())
  const navigation = useNavigate();

  const handleLogout = (e) => {
    e.preventDefault()
    document.cookie = `_token=; expires=Thu, 01 Jan 1970 00:00:00 UTC; path="/";max-age=0;`;
    navigation('/login');
  }
  return (
    <>

    {
      (type) ?  <a href="https://dashboard.tawk.to/?lang=en#/dashboard/64116ec231ebfa0fe7f2ac05" rel="noreferrer" target="_blank">Chat Admin</a> : <></>
    }

    
      <CDropdown variant="nav-item">
        <CDropdownToggle placement="bottom-end" className="py-0" caret={false}>
          {/* <CAvatar src={avatar8} size="md" /> */}
         <CIcon icon={cilUser} className="mx-2" /> {name}
        </CDropdownToggle>
        <CDropdownMenu className="pt-0" placement="bottom-end">
          <CDropdownDivider />
          <CDropdownItem href="#/change-password">
            <CIcon icon={cilLockLocked} className="me-2" />
            Change Password
          </CDropdownItem>
          <CDropdownItem href="#" onClick={handleLogout}>
            <CIcon icon={cilAccountLogout} className="me-2" onClick={handleLogout} />
            Logout
          </CDropdownItem>
        </CDropdownMenu>
      </CDropdown>
    </>
  )
}

export default AppHeaderDropdown
